<template>
  <div>
    <el-col :span="12" :offset="6">
      <el-card class="cardBox" shadow="always">
        <div class="flex-row-center" style="margin-bottom:20px;">
          <el-image v-if="status == 0" style="width: 100px; height: 100px" :src="require('../../assets/images/authWarning/yes.png')" fit="fill"></el-image>
          <el-image v-else style="width: 100px; height: 100px" :src="require('../../assets/images/authWarning/no.png')" fit="fill"></el-image>
        </div>
        <div style="margin-bottom:30px;">
          <p v-if="status == 0" class="p">恭喜，绑定成功</p>
          <p v-else class="p">抱歉，绑定失败</p>
        </div>
        <div style="padding:0 20px;margin-bottom:30px;">
          <p v-if="status == 1" class="sp">因微信限制，该账号已注册的开放平台账号不允许第三方操作，如需授权给筷赚，请先于微信后台解绑已绑定的开放平台账号。若继续绑定该账号，则会在店铺绑定多个相同主体小程序/公众号时，客户身份无法统一，即一个真实客户在不同小程序/公众号出现不同身份；也可选择在微信开方平台将该小程序/公众号解除关联后重新授权。</p>
          <p v-if="status == 2" class="sp">重新授权只可应用于更新原公众号授权信息，无法换绑公众号。如需换绑，请先解除绑定后重新选择，方可换绑新公众号。</p>
          <p v-if="status == 3" class="sp">授权管理权限不完整，请在授权时保持默认选择，把开发账号管理权限统一授权给筷赚；若您已授权其他第三方平台，请先于微信后台解绑已绑定的开放平台账号。</p>
          <p v-if="status == 4" class="sp">暂不支持发布个人小程序</p>
        </div>
        <div class="flex-row-center">
          <el-button @click="closePage">关闭本页</el-button>
        </div>
      </el-card>
    </el-col>
  </div>
</template>
<script>
export default {
  data(){
    return{
      status:'',
      wechatType:'',
    }
  },
  created(){
    this.wechatType = this.$route.query.wechatType
    this.status = this.$route.query.status
  },
  methods:{
    closePage(){
      console.log('执行了')
      var userAgent = navigator.userAgent;
      if (userAgent.indexOf("Firefox") != -1 || userAgent.indexOf("Chrome") !=-1) {
        window.location.href="about:blank";
        window.close();
      } else {
        window.opener = null;
        window.open("", "_self");
        window.close();
      }
    },
  }
}
</script>
<style lang="less" scoped>
  .cardBox{
    margin-top: 200px;
  }
  .p{
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    color: #303133;
  }
  .sp{
    text-align: center;
    font-size: 16px;
    line-height: 30px;
    font-weight: bold;
    color: #909399;
  }
</style>